import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { URL_PATHS } from '@shared';

const routes: Routes = [
  {
    path: '',
    children: [
      {
        path: URL_PATHS.LIST_APPLICATION,
        loadChildren: () => import('./list-s1-application/list-s1-application.module')
          .then(m => m.ListS1ApplicationModule)
      },
      {
        path: URL_PATHS.LIST_MULTIPLE_APPLICATION,
        loadChildren: () => import('./list-s1-application/list-s1-application.module')
          .then(m => m.ListS1ApplicationModule)
      },
      {
        path: URL_PATHS.LIST_SPECIAL_APPLICATION,
        loadChildren: () => import('./list-s1-application/list-s1-application.module')
          .then(m => m.ListS1ApplicationModule)
      },
      {
        path: URL_PATHS.LIST_ASSIGNED_APPLICATION,
        loadChildren: () => import('./list-s1-application/list-s1-application.module')
          .then(m => m.ListS1ApplicationModule)
      },
      {
        path: URL_PATHS.LIST_FOR_RELEASE_APPLICATION,
        loadChildren: () => import('./list-s1-application/list-s1-application.module')
          .then(m => m.ListS1ApplicationModule)
      },
      {
        path: URL_PATHS.RELEASED_APPLICATION,
        loadChildren: () => import('./list-s1-application/list-s1-application.module')
          .then(m => m.ListS1ApplicationModule)
      },
      {
        path: URL_PATHS.ADD_APPLICATION,
        loadChildren: () => import('./add-s1-application/add-s1-application.module')
          .then(m => m.AddS1ApplicationModule)
      },
      {
        path: URL_PATHS.UPDATE_APPLICATION + '/:id',
        loadChildren: () => import('./add-s1-application/add-s1-application.module')
          .then(m => m.AddS1ApplicationModule)
      },
      {
        path: URL_PATHS.ADD_MULTIPLE + '/:multipleId/:nextSequence',
        loadChildren: () => import('./add-s1-application/add-s1-application.module')
          .then(m => m.AddS1ApplicationModule)
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class S1ApplicationRoutingModule { }
